import React from 'react';
import './App.css';
import Header from './Header';
import Home from './Home';
import CheckOut from './CheckOut';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";

function App() {
  return (
    <div className="App">
      
     <Router>
   
       <Routes>
    
         <Route exact path = '/checkout' element={
         <>
         <Header />
         <CheckOut />
         </>
        }>
             

         </Route>
         <Route path='/' element={
            <>
            <Header />
            <Home />
            </>
         }>
              
         </Route>

       </Routes>

     </Router>
    
    </div>
  );
}

export default App;
